import topBar from '../components/topBar.vue'
import banner from '../components/banner.vue'
import goodList from '../components/goodList.vue'
export default {
  components: {
    topBar: topBar,
    goodList: goodList,
    banner: banner
  },
  computed: {
    token: function() {
      return this.$store.state.token
    }
  },
  data () {
    return {
      tabs: ['热门活动', '抽奖', '积分商场'],
      tabIndex: 0,                                     // 默认选中第一个
      slipeStyle: '',
      tabWidth: '',                                    // 每一个tab的宽度
      classMenu: ['抽奖记录', '进行中', '敬请期待'],     // 抽奖分类
      classIndex: 1,                                   // 默认选中进行中
      info: {
        level: 1,
        integral: 0
      },                                               // 首页详情
      goodList: ''
    }
  },
  onShow() {
    if(!this.info.user) {
      this.getDetail()
    }
    this.setLeftStyle(this.tabIndex)
  },
  methods: {
    
    // 积分商城
    getDetail() {
      let that= this
      let params = {
        url: that.$api.integralIndex
      }
      that.$httpRequest(params).then(res => {
        that.info= res.data.info
        let list= res.data.info.goods_list.data,
          arr= []
        list.map((item, index) => {
          let obj= {}
          obj.goods_id= item.goods_id
          obj.grade= item.sku[0].goods_price
          obj.name= item.goods_name
          obj.image= item.image
          arr.push(obj)
        })
        that.goodList= arr
      })
    },
    navigate(url) {
      uni.navigateTo({
        url: url
      })
    },
    
    // 选择热门活动，抽奖，或者积分商场
    selectTab(index) {
      this.tabIndex= index
      this.setLeftStyle(index)
    },
    
    // 设置底部slipe的偏移量
    setLeftStyle(index) {
      let that= this,
        left= ''
      let query = uni.createSelectorQuery().in(this);
      query.selectAll('.tab').boundingClientRect(data => {
        if(!that.tabWidth) {
          that.tabWidth= data[index].width
        }
        left =  that.tabWidth * index + that.tabWidth / 2 - 39
        that.slipeStyle= 'left: ' + left.toFixed(0) + 'px'
      }).exec();
    },
    
    // 选择抽奖分类
    selectMenu(index) {
      this.classIndex= index
    }
  }
}
